// Category Title
.category-title,
.category-list {
  font-size: 16px;
  line-height: 1.12;
  letter-spacing: -0.32px;
  color: var(--category-text-clr);

  @include tablet {
    font-size: 17px;
    line-height: 1.33;
    letter-spacing: -0.36px;
  }
  @include desktop {
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: -0.36px;
  }
}

.category-title {

    display: inline-block;
    margin-bottom: 24px;
    min-height: 18px;
    @include tablet {
        min-height: 24px;
    }  
    @include desktop{
        min-height: 24px;
    }       
}

.category-list {
    display: flex;
    flex-direction: column;
    gap: 24px;  
    @include tablet{
        gap: 32px;
    }
    @include desktop{
        gap: 32px;
    }
}

.category-title:hover,
.item-category:hover,
.category-title:focus,
.item-category:focus {
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--active-category-clr);

  @include tablet {
    font-size: 14px;
  }
  @include desktop {
    font-size: 15px;
  }
}

.active-category {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 15px;
  color: var(--active-category-clr);
}
//-----------scroll bar------------------------------

.scrollbar {

    padding-top: 10px;
    float: left;
    height: 228px;   
    width: 335px;
    overflow-y: scroll; 
    //margin-bottom: 40px; 
    
    @include tablet{
        width: 337px;
        height: 472px;
        //margin-bottom: 100px;
    }
    @include desktop{
        width: 328px;
        height: 472px;
        //margin-bottom: 86px;
    }
    
}


