.force-overflow {
  min-height: 450px;
}

#style-1::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: transparent;
}

#style-1::-webkit-scrollbar {
  width: 8px;
  height: 114px;
  background-color: transparent;
}

#style-1::-webkit-scrollbar-thumb {
  border-radius: 12px;
  background-color: var(--scroll-bar-clr);
}
